.menu {
  box-sizing: border-box;
  height: 100vh;
  background-color: #c2c2c2;
  font-weight: 600;
  text-transform: uppercase;
  overflow: auto;

  &__top {
    margin-bottom: 24px;

    position: sticky;
    top: 0;
    z-index: 1;
    background-color: #c2c2c2;;

    @include onTablet {
      margin-bottom: 8px;
    }
  }

  &__phone-number{
    display: block;
    text-decoration: none;
    color: $text-color-on-white;
    font-size: 16px;
    line-height: 27px;
    margin-top: 40px;
    margin-bottom: 16px;
  }

  &__call-to-order {
    display: block;
    text-decoration: none;
    color: $text-color-on-white;
    font-size: 12px;
    line-height: 16px;
    letter-spacing: 2px;

    padding-bottom: 7px;
    border-bottom: 1px solid $page-background;
    width: fit-content;
  }
}
